import React from 'react'


function Overview(){
    return (
        <div id="package-overview" className="z-depth-1">
            <h6 className="pac-display-text">Overview</h6>
            <hr />
            <p className="pac-p-data">Take up Chardham pilgrimage tour and explore the four main sites that are situated in the lap of Himalayas in Uttarakhand - Badrinath, Kedarnath, Gangotri and Yamunotri. It is among the most visited shrines by devotees from all across the globe. Enroute you can also visit various shrines and capture the mesmerizing view of Himalayas on camera.</p>
        </div>
    )
}

export default Overview;