import React from 'react'


function Inclusions(){

    let inclusions = [{data: "Sabkuch Milaega Kuch Nahi Chutega!!"},{data: "alaphalana dhimka dhikana"},{data: "alaphalana dhimka dhikana"}]

    return (
        <div id="package-inclusions" className="z-depth-1">
            <h6 className="pac-display-text">Inclusions</h6>
            <hr />

            <div className="pac-inclusions">
                {
                    inclusions.map((item,index)=>{
                        return (
                            <div className="pac-inclusion" key={`${index}`}>
                                <div className="circle"></div>
                                <h6>{ item.data }</h6>
                            </div>  
                        )                      
                    })
                }

                <br />
            </div>
        </div>
    )
}

export default Inclusions