import React from 'react'

function getIconName(inclusion){
    switch(inclusion){
        case "Flights":
            return "flight_takeoff";
        case "Hotel":
            return "hotel";
        case "Meals":
            return "restaurant_menu";
        case "Sighseeing":
            return "camera_alt"
        default:
            return "brightness_7"                
    }
}

function Features(){

    let inclusions = [{ name: "Flights" },{ name: "Meals" },{ name: "Hotel" },{ name: "Sighseeing" }]

    return (
        <div id="package-features" className="z-depth-1">
            <h6 className="pac-display-text">Features</h6>
            <hr />
            <div className="inclusions">
                {
                    inclusions.map((inclusion,index)=>{
                        return (
                            <div className="inclusion" key={`${index}`}>
                                <span><i className="material-icons">{ getIconName(inclusion.name) }</i></span>
                                <p>{ inclusion.name }</p>
                            </div>                                       
                        )
                    })
                }
            </div>            
        </div>
    )
}

export default Features;