import React from 'react'

let itenary = [
    {title: "Delhi - Risikesh",desc:"Take up Chardham pilgrimage tour and explore the four main sites."},
    {title: "Delhi - Risikesh",desc:"Take up Chardham pilgrimage tour and explore the four main sites."},
    {title: "Delhi - Risikesh",desc:"Take up Chardham pilgrimage tour and explore the four main sites."},
]

function Itenary(){
    
    return (
        <div id="package-itenary" className="z-depth-1">
            <h6 className="pac-display-text">Itenary</h6>
            <hr />
            <div className="package-itenary-items">
                {
                    itenary.map((item,index)=>{
                        return (
                            <div className="itenary-item" key={`${index}`}>
                                <img src="/images/day1.png" />
                                <div>
                                    <h6>{ item.title }</h6>
                                    <p>{ item.desc }</p>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
            <br />
        </div>
    )
}


export default Itenary;