import React from 'react'

function Package(){
    return (
        <div className="sim-pac">
            <img src="/images/dest5.jpg" />
            <h6 className="secondary-text" style={{fontWeight:'bold'}}>Another Tour</h6>
            <p>11N/12D</p>
        </div>
    )
}

function SimilarPackages(){
    return (
        <div id="similar-packages" className="z-depth-1">
            <h6 className="secondary-text" style={{fontWeight:'bold'}}>Similar Tours</h6>
            <hr />
            <div className="all-sim">
                <Package />
                <Package />
            </div>
            <br />
        </div>
    )
}

export default SimilarPackages